@import url('https://fonts.googleapis.com/css2?family=Edu+VIC+WA+NT+Beginner:wght@700&family=Ubuntu&display=swap');
@import url('https://fonts.googleapis.com/css2? family= Noto+Sans & display=swap');
:root {
    --main-color: #E9C229;
    --secondary-color: white;
    --third-color: #232C31;
    --title-links-font: 'Ubuntu',sans-serif;
}

* {
    margin: 0;
    padding: 0;
    font-family: Noto Sans, "sans empattement";
    /* transition: 1s; */
}

h1,h2,h3,h4,h5,h6{
font-family: var(--title-links-font);
color: var(--main-color);
}
h2{
    color: var(--third-color);
}

span{
    font-weight: 600;
}

body {
    background-color: var(--secondary-color);
    overflow-x: hidden;
}

header {
    width: 100%;
    height: 15vh;
    background-color: var(--secondary-color);
}

nav#nav-bar {
    width: 100%;
    height: 25%;
    position: relative;
    background-color: #E9C229;
    color: var(--third-color);
    display: flex;
    justify-content: center;
    align-items: center;
}
#nav-btns{
    display: none;
}

#logo{
    width: 100%;
    height: 75%;
    display: flex;
    justify-content: center;
    align-items: center;
}

img#logo-draw,
img#logo-words {
    height: 60px;
    width: auto;
}
#links-group{
    display: flex;
    justify-content: center;
    align-items: center;
}
#column-links-group{
    display: none;
}
#column-links-group.enable{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.link-item{
    margin: 0 30px;
    list-style: none;
    text-transform: uppercase;
}
.link-item a{
    color: var(--third-color);
    text-decoration: none;
}
.link.active{
    background-color: rgba(0, 0, 0, 0.09);
    color: var(--third-color);
    padding: 5px;
    border-bottom: 3px var(--third-color) solid;
}
.secondary-link-item {
    list-style: none;
    margin: 10px 0;
}
.secondary-link{
    text-decoration: none;
    color: var(--third-color);
}
.secondary-link.active{
    background-color: rgba(0, 0, 0, 0.09);
    color: var(--third-color);
    padding: 5px;
    font-weight: 600;
}

div#top-section{
    position: relative;
    background: transparent;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 85vh;
    color: var(--main-color);
    display: flex;
    justify-content: center;
    align-items: center;
}
#top-cover{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    filter: blur(4px);
    z-index: -3;
    object-fit: cover;
}
.div-slogan{
    width: 100%;
    min-height: 5cm;
    background-color: #00000084;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: -2;
}
#slogan{
    width: 70%;
    font-size: 75px;
    text-align: center;
    z-index: -1;
}
#slogan.main{
    color: var(--main-color);
    border-radius: 10px;
}
#slogan.secondary{
    color: var(--secondary-color);
}
#slogan.third{
    color: var(--third-color);
}
section{
    width: 100%;
    min-height: 300px;
    background-color: var(--secondary-color);
    border-top: 3px solid var(--main-color);
    border-bottom: 3px solid var(--main-color);
    border-right: 0;
    padding-bottom: 10px;
    transition: 1s;

}
section p.section-content, section h4{
    width: 90%;
    margin: 0 auto;
    font-size: 25px;
}
div.section-head{
    background-color: var(--main-color);
    color: var(--third-color);
    width: 45%;
    height: 1.5cm;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    transition: 1s;
}
div.section-head h3{
    font-size: 28px;
}
div#services-group{
    width: 100vw;
    min-height: 250px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.service-item{
    width: 150px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    text-decoration: none;
}
.service-item:hover svg{
    transform: scale(1.2);
    transition: 1s;
}
.service-item p{
    width: 95%;
    margin: 0;
}
.service-title{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.service-text{
    margin-left: 50px;
}
.service-title h4{
    font-size: 30px;
}
.img-service{
    height: 30px;
    width: auto;
    margin: 10px;
    fill: white;
}
h5{
    font-size: 25px;
    margin: 10px;
}
.service-text p{
    width: 80%;
    padding-left: 50px;
}
.right{
    text-align: right;
}
svg{
    height: 80px;
    width: auto;
    margin: 10px;
}
#modeles-text{
    margin-top: 70px;
}
.modele-group{
    width: 100vw;
    min-height: 10cm;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.modele-group img{
    height: 5cm;
    width: auto;
}
.modele-group .empty{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.contact-link svg {
    height: 40px;
    width: auto;
    fill: var(--third-color);
}
footer{
    width: 100vw;
    min-height: 15vh;
    background-color: var(--main-color);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.contact-title{
    color: var(--third-color);
    font-size: 22px;
}
.contact-div{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.contact-link{
    text-decoration: none;
    color: var(--third-color);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 10px;
}
@media (min-width: 1280px) {
    section p {
            color: var(--third-color);
        }
        section p.section-content{
            width: 70%;
            margin: 0 auto;
            padding: 30px;
            font-size: 25px;
        }
    section:hover div.section-head {
            width: 80vw;
            transition: 1s;
        }
    
        section:hover {
            background-color: var(--third-color);
            transition: 1s;
        }
        section:hover p{
            color: var(--secondary-color);
        }
        section:hover span{
            color: var(--main-color);
        }

        .service-item p{
            display: none;
        }
        .service-item:hover p{
            display: block;
        }
        section .service-item svg{
            fill: var(--third-color);
        }
        section:hover .service-item svg {
            fill: var(--main-color);
        }
        section .service-item h3{
            color: var(--third-color);
        }
        section:hover .service-item h3{
            color: var(--main-color);
        }
}
@media (max-width:1280px) {
    section p {
            color: var(--secondary-color);
        }
    section{
        background-color: var(--third-color);
    }
    span{
        color: var(--main-color);
    }
    #slogan{
        font-size: 50px;
    }
    div.section-head{
        width: 100vw;
    }
    div.section-head h2{
        font-size: 20px;
        padding-left: 10px;
    }
    #links-group{
        display: none;
    }
}
@media (max-width:500px) {/*Pour Smartphones*/
    nav#nav-bar{
        height: 1cm;
    }
        #nav-btns {
            position: absolute;
            left: 85%;
            display: flex;
            flex-direction: row-reverse;
            justify-content: center;
            align-items: center;
        }
    
        .nav-btn {
            height: 100%;
            width: auto;
            margin: 0 1px;
            background-color: transparent;
            border: 0;
        }
    
        .nav-btn svg {
            height: 25px;
            width: 25px;
            fill: var(--third-color);
        }
    
        #nav-menu {
            position: absolute;
            top: 100%;
            width: 100%;
            height: 0;
            background-color: var(--main-color);
            transition: 1s;
        }
    
        #nav-menu.enable {
            height: 90vh;
            transition: 1s;
            z-index: 10;
        }
    #top-cover{
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
    .section-head h2{
        font-size: 15px;
    }
    svg{
        fill: var(--main-color);
    }
    section p.section-content{
        width: 90%;
        margin: 0 auto;
        font-size: 25px;
        padding: 10px;
    }
    .contact-link {
        font-size: 15px;
    }
    .contact-link svg{
        height: 20px;
        width: auto;
    }
}
@media (min-width: 450px) {
    #links-group{
        display: flex;
    }
}
@media (min-width: 500px) {
    #links-group {
        font-size: 12px;
    }
}