:root {
    --main-color: #E9C229;
    --secondary-color: white;
    --third-color: #232C31;
    --title-links-font: 'Ubuntu', sans-serif;
}
/* Services */
div#head-div{
    background: #1a2d52;
    background-size: cover;
    background-position: 0 -350px;
}
div#head-div h1{
    text-align: center;
    padding-top: 30px;
}
div.full-head-section{
    width: 100%;
    background-color: var(--main-color);
    display: flex;
    justify-content: center;
    align-items: center;
}
div.full-head-section svg{
    height: 28px;
    width: auto;
    fill: var(--third-color);
}
div.full-head-section h2{
    font-size: 28px;
}
.content{
    width: 70%;
    margin: 0 auto;
}
.content-item{
    padding: 5px;
}
.content h3{
    font-size: 25px;
    margin: 15px 0 10px 0;
}
.content h4{
    font-size: 20px;
    margin: 2px;
    padding: 5px;
}
p.tab-p{
    margin-left: 25px;
}
/* Modeles */
.part-head{
    background-color: #1a2d52;
    height: 15vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 2px solid var(--main-color);
}
#models-group{
    width: 100%;
    min-height: 55vh;
    background-color: var(--third-color);
}
#models-group .empty{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 60px;
}
#models-group .empty img{
    height: 5cm;
    width: auto;
    margin-bottom: 20px;
}
#models-group .empty p{
    color: var(--secondary-color);
    font-size: 22px;
}
ul.enum{
    margin-left: 20px;
}
.mark{
    color: var(--main-color);
    font-weight: 600;
}
.not-found-div{
    width: 100%;
    height: 70vh;
    background-color: var(--third-color);
    color: var(--secondary-color);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.not-found-div img{
    height: 3cm;
    width: auto;
}
.not-found-div h2{
    color: var(--secondary-color);
    margin: 20px 0 30px 0;
}
.not-found-div a{
    background-color: var(--main-color);
    color: var(--third-color);
    text-decoration: none;
    padding: 20px;
}
@media (min-width: 1280px){
    div#presentation-div{
        background: url(../img/Présentation-cover-1.png) no-repeat center;
        background-size: cover;
        width: 100vw;
        height: 70vh;
    }
    .about-us-text{
        padding: 10px;
        width: 65%;
        height: 15cm;
    }
}
/*Pour Smartphones*/
@media (max-width:500px) {
    a.service-item{
        font-size: 10px;
    }
    a.service-item svg{
        width: 50px;
        height: auto;
    }
    div.full-head-section h2{
        font-size: 20px;
    }
    .content {
        width: 85vw;
        margin: 0 auto;
    }
    div#presentation-div {
        background: var(--third-color);
        width: 100vw;
        height: 70vh;
    }
    .about-us-head{
        background-color: #1a2d52;
        width: 100%;
        height: 2cm;
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: 2px solid var(--main-color);
    }
    .about-us-head h2{
        color: var(--main-color);
    }

    .about-us-text {
        padding: 10px;
        color: var(--secondary-color);
    }
}